import React from "react";
import styled from "styled-components";

//  -   !   ! 2023 
function History() {
  const data = [
    { year: "2022", contents: ["  R&D   "] },
    {
      year: "2021",
      contents: ["  R&D  ", "   "],
    },
    {
      year: "2020",
      contents: [
        " ` `",
        " TP `   `",
        "     ",
      ],
    },
    {
      year: "2019",
      contents: [
        "       ",
        "  ",
        "  ()",
      ],
    },
    {
      year: "2018",
      contents: [
        "SW  ",
        "   ",
        "   ",
        "()  ",
      ],
    },
    {
      year: "2017",
      contents: [
        "  ",
        "  ",
        "     ",
        "  ",
      ],
    },
    { year: "2016", contents: ["   ", " "] },
  ];

  return (
    <Container>
      <Title1st>HISTORY</Title1st>
      <Title2nd>A.I.   </Title2nd>
      {data.map((item, index) => (
        <YearItem key={index}>
          <Year>{item.year}</Year>
          <Divider />
          <Contents>
            {item.contents.map((content, idx) => (
              <ContentItem key={idx}>{content}</ContentItem>
            ))}
          </Contents>
        </YearItem>
      ))}
    </Container>
  );
}

export default History;

// Title   

const Title1st = styled.p`
  font-family: "Pretendard-Bold";
  font-size: 35px;
  color: #0072ff;
  margin-bottom: -7px;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (min-width: 360px) and (max-width: 767px) {
    font-size: 21px;
  }
`;

const Title2nd = styled.p`
  font-family: "Pretendard-Bold";
  font-size: 35px;
  margin-bottom: 50px;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (min-width: 360px) and (max-width: 767px) {
    font-size: 21px;
  }
`;

const Container = styled.div`
  display: flex;
  padding: 30px;
  flex-direction: column;
`;

const YearItem = styled.div`
  display: flex;
  flex-direction: row;
  margin-bottom: 30px;
  align-items: flex-center;
  justify-content: flex-center;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (min-width: 360px) and (max-width: 767px) {
    font-size: 21px;
  }
`;

const Year = styled.div`
  font-family: "Pretendard-Bold";
  font-size: 35px;
  margin-right: 30px;
  width: 60px;
  text-align: right;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (min-width: 360px) and (max-width: 767px) {
    font-size: 21px;
  }
`;

const Divider = styled.div`
  height: 30px;
  width: 2px;
  background-color: #000;
  margin: 12px;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (min-width: 360px) and (max-width: 767px) {
    font-size: 19px;
  }
`;

const Contents = styled.div`
  font-family: "Pretendard-Bold";
  font-size: 20px;
  display: flex;
  flex-direction: column;
  padding-top: 10px;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (min-width: 360px) and (max-width: 767px) {
    font-size: 19px;
  }
`;

const ContentItem = styled.div`
  margin-bottom: 12px;

  @media all and (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
  }

  @media all and (min-width: 360px) and (max-width: 767px) {
    font-size: 19px;
  }
`;
